@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

body {
  margin: 0;
  font-family: 'Poppins', sans-serif;
  font-size: 0.83vw;
  font-weight: 400;
  color: #191919;
}

ul, p {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

input {
  padding: 0;
  margin: 0;
  border: 0;
  border: none;
}

button {
  padding: 0;
  border: none;
  cursor: pointer;
  background: transparent;
}

a:link, a:visited {
  color: #191919;
  outline: none;
  text-decoration: none;
  cursor: pointer;
}

h5 {
  margin: 0;
  font-weight: normal;
  font-size: 0.93vw;
  text-transform: uppercase;
}

.general-container {
  display: flex;
  justify-content: space-around;
}

/* header general rules */

.header-container,
.news-container,
.promo-container,
.header-block,
.news-titles,
.news-date,
.news-block,
.exchange-block,
.exchange-item,
.menu-btn {
  padding: 0.52vw;
  border: 0.26vw solid #4c5760;
}

.news-container,
.promo-container,
.header-block,
.news-titles,
.news-date,
.news-block,
.exchange-block,
.news-list {
  margin-top: 0.52vw;
}

.news-icon,
.promo-img {
  border: 0.15vw solid #4c5760;
}

/* header, news, exchange, ad */

.header-container {
  margin: 2.6vw;
  padding-top: 0.52vw;
  background-color: #93a8ac;
}

.news-container {
  width: 41.6vw;
  background-color: #66635b;
}

.gen-container-hb, .gen-container-exb {
  display: flex;
}

.gen-container-exb {
  align-items: center;
}

.header-block, .news-block, .exchange-block, .promo-container {
  background-color: #d7ceb2;
}

.news-titles, .news-date, .exchange-item {
  background-color: #a59e8c;
}

.news-titles {
  display: flex;
  justify-content: space-around;
  width: 26.04vw;
}

.news {
  display: flex;
  align-items: center;
  margin-bottom: 0.52vw;
}

.news-icon {
  width: 1.04vw;
  height: 1.04vw;
}

.news-header {
  margin-left: 0.52vw;
}

.news-icon, .menu-btn, .promo-img {
  background-color: #93a8ac;
}

.news-date {
  width: 10.41vw;
  margin-left: 0.52vw;
  text-align: center;
}

.exchange-list {
  display: flex;
}

.exchange-item {
  display: flex;
  justify-content: space-around;
  width: 7.81vw;
  margin-top: 0.52vw;
  margin-right: 0.52vw;
}

.menu-btn {
  width: 3.64vw;
  height: 2.6vw;
  padding: 0.26vw;
  font-size: 0.52vw;
  text-align: center;
}

.promo-container {
  width: 18.22vw;
  height: 10.41vw;
  margin-left: 0.52vw;
}

.promo-block {
  display: flex;
  flex-direction: column;
}

.promo-img {
  width: 5.2vw;
  height: 5.2vw;
}

.promo-title {
  font-size: 1.04vw;
}

/* main general rules */

.general-main {
  display: flex;
  flex-direction: column;
}

.main-container,
.menu-container,
.search-logo-container,
.banner-container {
  padding: 0.52vw;
  border: 0.26vw solid #7c6c77;
}

.menu-container,
.search-logo-container,
.logo-img,
.banner-container,
.banner-block {
  margin-top: 0.52vw;
}

/* main, logo, search, banner */

.main-container {
  margin: 2.6vw;
  padding-top: 0.52vw;
  background-color: #aaa694;
}

.menu-container {
  background-color: #ebf8b8;
}

.menu-block {
  display: flex;
  justify-content: center;
}

.main-menu {
  display: flex;
  flex-wrap: wrap;
}

.menu-list:nth-child(n + 2) {
  margin-left: 1.04vw;
}

.btn-more, .submit-btn {
  margin-left: 1.04vw;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 0.83vw;
  color: #191919;
}

.search-logo-container {
  background-color: #ffe787;
}

.search-logo-box {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.logo-img {
  border-color: #7c6c77; 
  background-color: #aaa694;
}

.search-block {
  display: flex;
  flex-direction: column;
  margin-left: 0.52vw;
}

.search-form {
  display: flex;
}

.search-field {
  height: 2.6vw;
  width: 77.08vw;
  margin-right: 0.52vw;
  border: 0.1vw solid #7c6c77;
  background-color: #fff; 
}

.submit-btn {
  height: 2.81vw;
  width: 6.25vw;
  margin-left: 0;
  border: 0.1vw solid #7c6c77;
  background-color: #ebf8b8;
}

.search-hint {
  margin-top: 0.52vw;
}

.banner-container {
  background-color: #d1d0a3;
}

.banner-block {
  display: flex;
  justify-content: center;
}

.banner {
  width: 78.12vw;
  border-color: #7c6c77;
  background-color: #ebf8b8;
}

/* footer general rules */

.footer-container,
.widget {
  padding: 0.52vw;
  border: 0.26vw solid #4d6a6d;
}

/* footer */

.footer-container {
  margin: 2.6vw;
  padding-top: 0.52vw;
  background-color: #798478;
}

.widgets-container {
  margin-top: 0.52vw;
}

.widgets-list {
  display: flex;
  flex-wrap: wrap;
  width: 88.54vw;
  justify-content: space-around;
}

.widget {
  background-color: #eae0cc;
}

.widget-body {
  margin-top: 0.52vw;
}

.weather-box {
  display: flex;
  align-items: center;
}

.weather-img {
  height: 2.6vw;
  width: 2.6vw;
  margin-right: 0.52vw;
  background-color: #a0a083;
}

.current-weather {
  margin-right: 0.52vw;
  font-family: 'Poppins', sans-serif;
  font-weight: 400;
  font-size: 1.56vw;
  color: #191919;
}

.total-indicators {
  display: flex;
  flex-direction: column;
}

.widgets-item-body {
  display: flex;
  align-items: center;
}

.widgets-list-item:nth-child(n + 2) {
  margin-top: 0.52vw;
}

.broadcast-img {
  height: 1.04vw;
  width: 1.04vw;
  margin-right: 0.52vw;
  background-color: #c9ada1;
}

.broadcast-hint {
  margin-left: 0.52vw;
  opacity: 0.5;
}

.program-name {
  margin-left: 0.78vw;
  margin-right: 0.78vw;
}

.program-channel {
  text-transform: uppercase;
  opacity: 0.5;
}
